.choosing-box {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-image: url("../../assets/images/wallpapers/horizon-zero-dawn-wallpaper.jpg");
  background-position: center;
  overflow: hidden;
}
.choosing-box:-webkit-scrollbar {
  display: none;
}
.choosing-box .choosing-header {
  width: 100%;
  height: 8%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid #000;
  background-color: rgb(46, 46, 132);
  color: rgb(183, 183, 183);
  font-family: "Montserrat", sans-serif;
  font-size: 30px;
}
.choosing-box .choosing-players-box {
  width: 100%;
  height: 74%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: auto;
  backdrop-filter: blur(5px);
}
.choosing-box .choosing-players-box .choosing-players-messgage {
  align-self: flex-start;
  animation: zoom-in-zoom-out 2s ease-out infinite;
  font-size: 25px;
  font-weight: bold;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  position: fixed;
  top: 19%;
  left: 11%;
}
.choosing-box .choosing-players-box .choosing-players-area {
  width: 75%;
  height: 85%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 60px;
  align-items: center;
}
.choosing-box .choosing-footer {
  width: 100%;
  height: 18%;
  display: grid;
  grid-template-areas: "p1 p2" "footer footer";
  grid-template-rows: 1fr 1fr;
  backdrop-filter: blur(5px);
}
.choosing-box .choosing-footer .choosing-footer-area {
  grid-area: footer;
  background-color: orange;
  border-top-left-radius: 70%;
  border-top-right-radius: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
}
.choosing-box .choosing-footer .choosing-footer-area p {
  font-size: 50px;
  font-family: "Tiro Telugu", serif;
  cursor: pointer;
}
.choosing-box .choosing-footer .choosing-footer-area:hover {
  color: red;
}

@keyframes zoom-in-zoom-out {
  0% {
    transform: scale(1, 1) rotate(-35deg);
  }
  50% {
    transform: scale(1.5, 1.5) rotate(-35deg);
  }
  100% {
    transform: scale(1, 1) rotate(-35deg);
  }
}
@media (max-height: 750px) {
  .choosing-box .choosing-footer {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    backdrop-filter: blur(5px);
  }
  .choosing-box .choosing-footer .choosing-p1 {
    order: 1;
  }
  .choosing-box .choosing-footer .choosing-p2 {
    order: 3;
  }
  .choosing-box .choosing-footer .choosing-footer-area {
    order: 2;
    background-color: orange;
    border-top-left-radius: 0%;
    border-top-right-radius: 0%;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.5s;
    padding: 10px 30px;
  }
  .choosing-box .choosing-footer .choosing-footer-area p {
    font-size: 30px;
  }
}
@media (max-width: 1200px) {
  .choosing-box .choosing-header {
    display: none;
  }
  .choosing-box .choosing-header-fake {
    height: 20vh;
    backdrop-filter: blur(5px);
  }
}