.choosing-p1 {
    grid-area: p1;
    display: flex;
    align-items: center;

    .choosing-p1-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 130px;
        padding: 15px;
        margin-left: 50px;
        border-radius: 10px;
        cursor: pointer;
    }
}

.choosing-player-box-activated {
    background-color: green;
    transition: 0.3s;

    &:hover {
        background-color: lightgreen;
    }
}

.choosing-player-box-deactivated {
    background-color: orangered;
    transition: 0.3s;

    &:hover {
        background-color: yellow;
    }
}