.loading-bar-progress {
    display: flex;
    flex-direction: column-reverse;
    gap: 10px;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    color: white;
    font-family: 'Shantell Sans', cursive;
}

@media (max-width: 2000px){
    .loading-bar-progress {
        left: 37%;
    }
}

@media (max-width: 1750px){
    .loading-bar-progress {
        left: 36%;
    }
}

@media (max-width: 1650px){
    .loading-bar-progress {
        left: 35%;
    }
}

@media (max-width: 1550px){
    .loading-bar-progress {
        left: 34%;
    }
}
